import { Component, ViewChild } from '@angular/core';
import { AlertController,ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { ImageProvider } from '../../../providers/image/image';
import { Camera } from '@ionic-native/camera';
import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-plantilladm-create',
  templateUrl: 'plantilladm-create.html',
})
export class PlantilladmCreatePage {
  @ViewChild('fileInput') fileInput;

  
  public form                  : FormGroup;
  username: string = "";
  public nombre                 : any;
  public fechaini		     = (new Date()).toISOString();
  public imagen		     : any;
  public fechafin		    = (new Date()).toISOString();
  public ubicacion		     : any;
  public estado		     : any;
  descripcion:any;
  public orden		     : any;

  private _ID                  : String;
  public foto              	: any;
  public pageTitle           	: string;
  public image              	: any;
  idplantilla: any;
  idempresa: any;

  isDisablednit :any;
  constructor( public alertCtrl: AlertController,public storage: Storage, private _IMAGE       : ImageProvider, public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,  
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController, 
          public events: Events)
  {
     // Define form validation - VERY basic!
     this.form = this._FB.group({
        nombre     : ['', Validators.required],
        descripcion     : ['', Validators.required],
        fechaini     : ['', Validators.required],
        fechafin     : ['', Validators.required],
        ubicacion     : ['', Validators.required],
        estado     : ['', Validators.required],
        orden     : ['', Validators.required],
       
        imagen     : ['', Validators.required]
       
     });



  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 


  
  }

  ionViewDidLoad() : void
  {

    this.idempresa=this.navParams.data.idempresa;
    this.idplantilla=this.navParams.data.idplantilla;

    if(this.navParams.get("record")==null)
    {

   
      this.orden=10;

      this.authService.getReg(this.idplantilla + '/'+  this.idempresa + '/orden','plantilladms').then((data) => {
    
        if(data)
        {
          var aa:any=data;
        
          this.orden=aa.orden;
          this.orden=this.orden+10;
        }
        
      
      }, (err) => {
        this.msj.showError3('Error',err.error);
      // this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })  
      });


       
      this.translateService.get('ITEM_CREATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
      
      this.isDisablednit =true
    }
    else {
      
       this._ID 				=	this.navParams.data.record._id;
       this.descripcion		=	this.navParams.data.record.descripcion;
       this.nombre		=	this.navParams.data.record.nombre;
       this.fechaini		=	this.navParams.data.record.fechaini;
   
       this.fechafin		=	this.navParams.data.record.fechafin;
     
       this.ubicacion		=	this.navParams.data.record.ubicacion;
      
   
       this.foto		=	this.navParams.data.record.foto;
       this.image	=	this.navParams.data.record.foto;
       this.imagen 	=	this.navParams.data.record.foto;
       this.estado		=	this.navParams.data.record.estado;
       this.orden		=	this.navParams.data.record.orden;
       this.isDisablednit =false
       this.translateService.get('ITEM_UPDATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    

    }
  }

  


  manageGallery() : void
  {
  let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }

     let   options       : any	     = { 
       idempresa:this.idempresa,
       idplantilla:this.idplantilla,
     nombre : this.form.controls['nombre'].value,
     descripcion : this.form.controls['descripcion'].value,
     fechaini : this.form.controls['fechaini'].value,
     fechafin : this.form.controls['fechafin'].value,
     ubicacion : this.form.controls['ubicacion'].value,
   
     foto : this.imagen,
     estado : this.form.controls['estado'].value,
     orden : this.form.controls['orden'].value,
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' plantilla unidades '}
    };
      

     if(this.navParams.get("record")==null)
     {  
          if(options){
            this.authService.createReg('',options,'plantilladms').then((result) => {
              if (!this.form.valid) { return; }
              console.log(result)
              this.viewCtrl.dismiss(result);
          }, (err) => {  
            this.msj.showError3('Error',err.error);
          });
          }
     }   
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'plantilladms').then((result) => {
           if (!this.form.valid) { return; }
           console.log(result)
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }

  
  updateProfileImage() {
    let t1,t2;
    this.translateService.get('SELECCIONE_IMAGEN').subscribe((value) => {
      t1=value;
    });
    this.translateService.get('TOME_IMAGEN').subscribe((value) => {
      t2=value;
    });
    let actionSheet = this.actionSheetCtrl.create({
      title: t1,
      buttons: [//1930
        {
          text: t2,
          handler: () => {
            this.takePhotograph();
          }
        },
        {
          text: t1,
          handler: () => {
            this.selectImage();
          }
        }
      ]
    });
    actionSheet.present();
  }

  takePhotograph() : void
  {
   if (Camera['installed']()) {
     this._IMAGE
     .takePhotograph()
     .then((image)=>
     {
        this.imagen   	= image.toString();
        this.image   		= image.toString();
     })
     .catch((err)=>
     {
        console.log(err);
     });
   }
   else{
     this.fileInput.nativeElement.click();
   }
  }

  
  selectImage() : void
  {
   if (Camera['installed']()) {
     this._IMAGE
     .selectPhotograph()
     .then((image)=>
     {
        this.imagen  	= image.toString();
        this.image   		= image.toString();
     })
     .catch((err)=>
     {
        console.log(err);
     });
   }
   else{
     this.fileInput.nativeElement.click();
   }   
  }
  
  public processWebImage(event): void {
    if(event.target.files && event.target.files[0]){
          const reader: FileReader = new FileReader();
          reader.onload = (readerEvent: any) => {
          this.image = readerEvent.target.result;
          this._IMAGE.generateFromImage(this.image, 200, 200, 0.5, data => {
          this.image = data;
          this.imagen =  data;
      });
   };
      reader.readAsDataURL(event.target.files[0]);
    }
}



}



